<template>
  <div class="w-full">
    <div
      class="fila"
      :class="{
        'border-yellow-500': status === 'PROCESO',
        'border-green-500': status === 'SOLUCIONADO',
        'border-blue-500': status === 'PLANIFICADO',
        'border-gray-500': status === 'Varios',
      }"
      @click="comprobarArbirEnlace(array)"
    >
      <!-- SECCIÓN -->
      <div class="w-1/2 truncate md:w-seccion">
        <h1 class="md:hidden text-xxs uppercase font-bold text-gray-500">
          Sección:
        </h1>
        <h2 class="font-bold uppercase text-xs md:text-center w-full">
          {{ array.seccion }}
        </h2>
      </div>

      <!-- SUBSECCIÓN -->
      <div class="w-1/2 truncate md:w-1/6">
        <h1 class="md:hidden text-xxs uppercase font-bold text-gray-500">
          Subsección:
        </h1>
        <h2 class="font-bold uppercase text-xs md:text-center w-full">
          {{ array.subseccion }}
        </h2>
      </div>

      <!-- TIPO DE EQUIPO -->
      <div class="w-1/2 truncate md:w-1/6">
        <h1 class="md:hidden text-xxs uppercase font-bold text-gray-500">
          Tipo:
        </h1>
        <h2 class="font-bold uppercase text-xs md:text-center w-full">
          {{ array.tipo }}
        </h2>
      </div>

      <!-- MÁS DE UN PLAN -->
      <div class="w-1/2 truncate md:w-1/6">
        <h1 class="md:hidden text-xxs uppercase font-bold text-gray-500">
          Plan:
        </h1>
        <!-- UN PLAN -->
        <h2
          v-if="array.totalPlaneaciones == 1"
          class="font-bold uppercase text-xs md:text-center w-full"
        >
          {{ frecuencia }}
        </h2>

        <!-- MÁS DE UN PLAN -->
        <h2
          v-else
          class="font-bold uppercase text-xs md:text-center w-full text-red-300"
        >
          Varios ({{ array.totalPlaneaciones }})
        </h2>
      </div>

      <!-- NOMBRE DEL EQUIPO -->
      <div class="w-full md:w-xx">
        <h1 class="md:hidden text-xxs uppercase font-bold text-gray-500">
          Equipo:
        </h1>
        <h2 class="font-bold uppercase text-xs">{{ array.equipo }}</h2>
      </div>
      <div class="w-full hidden md:flex md:w-1/6">
        <h1 class="md:hidden text-xxs uppercase font-bold text-gray-500">
          Estado:
        </h1>
        <h2
          v-if="array.totalPlaneaciones == 1"
          class="font-bold uppercase text-xs md:text-center w-full"
          :class="{
            'text-yellow-500': status === 'PROCESO',
            'text-green-500': status === 'SOLUCIONADO',
            'text-blue-500': status === 'PLANIFICADO',
          }"
        >
          {{ status }}
        </h2>
        <button
          @click="showhijos = !showhijos"
          v-else
          class="
            w-3/4
            mx-auto
            rounded
            bg-purple-100
            text-purple-500 text-xs
            py-1
            focus:outline-none
          "
        >
          <span v-if="!showhijos">VISUALIZAR</span>
          <span v-if="showhijos">OCULTAR</span>
        </button>
      </div>

      <div v-if="array.totalPlaneaciones > 1" class="w-full md:hidden">
        <button
          @click="showhijos = !showhijos"
          class="
            w-full
            mx-auto
            rounded
            bg-purple-100
            text-purple-500 text-xs
            py-1
            focus:outline-none
          "
        >
          <span v-if="!showhijos">VISUALIZAR</span>
          <span v-if="showhijos">OCULTAR</span>
        </button>
      </div>
    </div>

    <!-- HIJOS -->
    <div class="w-full" v-if="array.totalPlaneaciones > 1 && showhijos">
      <div
        v-for="(item, index) in array.planeaciones"
        :key="index"
        class="
          cursor-pointer
          mb-2
          md:mb-0
          hover:bg-gray-100
          md:rounded-r-none
          flex flex-row
          items-center
          flex-wrap
          justify-start
          border-l-4
          rounded-r-md
          px-2
          py-2
          md:py-5
          w-full
          text-left
        "
        :class="{
          'border-yellow-500': item.status === 'PROCESO',
          'border-green-500': item.status === 'SOLUCIONADO',
          'border-blue-500': item.status === 'PLANIFICADO',
        }"
        @click="abrirEnlace(item)"
      >
        <!-- SECCIÓN -->
        <div class="w-1/2 truncate md:w-seccion">
          <h1 class="md:hidden text-xxs uppercase font-bold text-gray-500">
            Sección:
          </h1>
          <h2 class="font-bold uppercase text-xs md:text-center w-full">
            {{ array.seccion }}
          </h2>
        </div>

        <!-- SUBSECCIÓN -->
        <div class="w-1/2 truncate md:w-1/6">
          <h1 class="md:hidden text-xxs uppercase font-bold text-gray-500">
            Subsección:
          </h1>
          <h2 class="font-bold uppercase text-xs md:text-center w-full">
            {{ array.subseccion }}
          </h2>
        </div>

        <!-- TIPO -->
        <div class="w-1/2 truncate md:w-1/6">
          <h1 class="md:hidden text-xxs uppercase font-bold text-gray-500">
            Tipo:
          </h1>
          <h2 class="font-bold uppercase text-xs md:text-center w-full">
            {{ array.tipo }}
          </h2>
        </div>

        <!-- FRECUENCIA -->
        <div class="w-1/2 truncate md:w-1/6">
          <h1 class="md:hidden text-xxs uppercase font-bold text-gray-500">
            Plan:
          </h1>
          <h2 class="font-bold uppercase text-xs md:text-center w-full">
            {{ item.frecuencia }}
          </h2>
        </div>

        <!-- EQUIPO -->
        <div class="w-full md:w-xx">
          <h1 class="md:hidden text-xxs uppercase font-bold text-gray-500">
            Equipo:
          </h1>
          <h2 class="font-bold uppercase text-xs">{{ array.equipo }}</h2>
        </div>

        <!-- STATUS -->
        <div class="w-full hidden md:flex md:w-1/6">
          <h1 class="md:hidden text-xxs uppercase font-bold text-gray-500">
            Estado:
          </h1>
          <h2
            class="font-bold uppercase text-xs md:text-center w-full"
            :class="{
              'text-yellow-500': item.status === 'PROCESO',
              'text-green-500': item.status === 'SOLUCIONADO',
              'text-blue-500': item.status === 'PLANIFICADO',
            }"
          >
            {{ item.status }}
          </h2>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from "@vue/reactivity";
export default {
  props: {
    array: {
      type: Object,
      required: true,
    },
  },

  setup(props) {
    const status = ref("varios");
    const frecuencia = ref("");
    const showhijos = ref(false);

    if (props.array.planeaciones.length == 1) {
      props.array.planeaciones.forEach((item) => {
        status.value = item.status;
        frecuencia.value = item.frecuencia;
      });
    }

    const abrirEnlace = (item) => {
      localStorage.setItem("URL", "");

      if (item.idEquipo != "" && item.semana != "" && item.idOT > 0) {
        localStorage.setItem(
          "URL",
          `0;0;${item.idEquipo};${item.semana};${item.idPlan}`
        );
        window.open(
          "../OT/index.php",
          "OT",
          "directories=no, location=no, menubar=no, scrollbars=yes, statusbar=no, tittlebar=no, width=1200px, height=650px"
        );
      } else {
        alert("No se encontro la OT", "", "info", 1400);
      }
    };

    const comprobarArbirEnlace = (array) => {
      if (array.planeaciones.length == 1) abrirEnlace(array.planeaciones[0]);
    };

    return { showhijos, status, frecuencia, comprobarArbirEnlace, abrirEnlace };
  },
};
</script>

<style scoped>
.fila {
  @apply cursor-pointer mb-2 md:mb-0 hover:bg-gray-100 md:rounded-r-none flex flex-row items-center flex-wrap justify-start bg-white shadow border-l-4 rounded-r-md px-2 py-2 md:py-5 w-full text-left;
}

@media (min-width: 768px) {
  .md\:w-xx {
    width: 20%;
  }
}
@media (min-width: 768px) {
  .md\:w-seccion {
    width: 10%;
  }
}
</style>
