import { createRouter, createWebHashHistory } from 'vue-router'
import Planeaciones from '../views/Planeaciones.vue'
import Gantt from '../views/Ganttviewer.vue';

const routes = [
  {
    path: "/:catchAll(.*)",
    name: 'Planeaciones',
    component: Planeaciones
  },

  {
    path: '/gantt',
    name: 'Gantt',
    component: Gantt
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
