<template>
  <div class="cuadro w-6 h-6 cursor-pointer" :class="[array.status]"></div>
</template>

<script>
import { getISOWeek } from "date-fns";

export default {
  props: { array: { type: Object, required: false } },

  setup() {
    const fechaHoy = new Date();

    const semanaActual = getISOWeek(
      new Date(fechaHoy.getFullYear(), fechaHoy.getMonth(), fechaHoy.getDate())
    );
    return { semanaActual };
  },
};
</script>

<style scoped>
.cuadro {
  border-top-width: 2px;
  border-bottom-width: 2px;
  border-left-width: 2px;
}

.cuadro:last-child {
  border-right-width: 2px;
}

.NINGUNO {
  background-color: white;
}

.PROCESO {
  background-color: rgb(255, 255, 118) !important;
}

.SOLUCIONADO {
  background-color: rgb(115, 238, 115) !important;
}

.PLANIFICADO {
  background-color: rgb(98, 157, 252) !important;
}
</style>
