<template>
  <div class="w-full h-screen overflow-y-auto bg-gris-30 p-2">
    <div class="w-full p-6 font-bold">
      <h1>GANTT VIEWER</h1>
    </div>
    <div class="flex flex-col items-center justify-start w-full">
      <!-- filtros -->
      <div
        class="
          w-full
          p-4
          flex flex-row flex-wrap
          items-end
          justify-start
          md:justify-center
          text-xs
        "
      >
        <!-- DESTINO -->
        <div class="w-1/2 md:w-1/12 p-2">
          <h1 class="font-bold uppercase w-full text-left">
            <span
              v-if="!obligatorios.idDestino"
              class="text-red-600 animated-pulse"
              >*
            </span>
            Destino
          </h1>
          <select
            class="bg-white p-2 rounded shadow w-full"
            v-model="parametros.idDestino"
            @change="obligatorios.idDestino = true"
          >
            <option value="0">Seleccion el Destino</option>
            <option
              v-for="(item, index) in arrayDestinos"
              :key="index"
              :value="item.idDestino"
            >
              {{ item.destino }}
            </option>
          </select>
        </div>

        <!-- SECCIÓN -->
        <div class="w-1/2 md:w-1/12 p-2">
          <h1 class="font-bold uppercase w-full text-left">
            <span
              v-if="!obligatorios.idSeccion"
              class="text-red-600 animated-pulse"
              >*
            </span>
            Sección
          </h1>
          <select
            class="bg-white p-2 rounded shadow w-full"
            v-model="parametros.idSeccion"
            @change="
              subecciones();
              obligatorios.idSeccion = true;
            "
          >
            <option value="0">Seleccione la Sección</option>
            <option
              v-for="(item, index) in arraySecciones"
              :key="index"
              :value="item.idSeccion"
            >
              {{ item.seccion }}
            </option>
          </select>
        </div>

        <!-- SUBSECCIÓN -->
        <div class="w-1/2 md:w-1/12 p-2">
          <h1 class="font-bold uppercase w-full text-left">Subsección</h1>
          <select
            class="bg-white p-2 rounded shadow w-full"
            v-model="parametros.idSubseccion"
          >
            <option value="0">TODOS</option>
            <option
              v-for="(item, index) in arraySubsecciones"
              :key="index"
              :value="item.idSubseccion"
            >
              {{ item.subseccion }}
            </option>
          </select>
        </div>

        <!-- TIPO -->
        <div class="w-1/2 md:w-1/12 p-2">
          <h1 class="font-bold uppercase w-full text-left">tipo</h1>
          <select
            class="bg-white p-2 rounded shadow w-full"
            v-model="parametros.idTipo"
          >
            <option value="0">TODOS</option>
            <option
              v-for="(item, index) in arrayTipos"
              :key="index"
              :value="item.idTipo"
            >
              {{ item.tipo }}
            </option>
          </select>
        </div>

        <!-- STATUS -->
        <div class="w-1/2 md:w-1/12 p-2">
          <h1 class="font-bold uppercase w-full text-left">Estado</h1>
          <select
            class="bg-white p-2 rounded shadow w-full"
            v-model="parametros.status"
          >
            <option value="TODOS">TODOS</option>
            <option
              v-for="(item, index) in arrayStatus"
              :key="index"
              :value="item"
            >
              {{ item }}
            </option>
          </select>
        </div>

        <!-- FRECUENCIA -->
        <div class="w-1/2 md:w-1/12 p-2">
          <h1 class="font-bold uppercase w-full text-left">Plan</h1>
          <select
            class="bg-white p-2 rounded shadow w-full"
            v-model="parametros.idFrecuencia"
          >
            <option value="0">TODOS</option>
            <option
              v-for="(item, index) in arrayFrecuencias"
              :key="index"
              :value="item.idFrecuencia"
            >
              {{ item.frecuencia }}
            </option>
          </select>
        </div>

        <!-- AÑO -->
        <div class="w-1/2 md:w-1/12 p-2">
          <h1 class="font-bold uppercase w-full text-left">Año</h1>
          <select
            class="bg-white p-2 rounded shadow w-full"
            v-model="parametros.año"
          >
            <option
              v-for="(item, index) in arrayAños"
              :key="index"
              :value="item"
            >
              {{ item }}
            </option>
          </select>
        </div>

        <!-- SEMANA INICIAL -->
        <div class="w-1/2 md:w-1/12 p-2">
          <h1 class="font-bold uppercase w-full text-left">
            <span
              v-if="!obligatorios.semanaInicial"
              class="text-red-600 animated-pulse"
              >*
            </span>
            Semana inicial
          </h1>
          <select
            class="bg-white p-2 rounded shadow w-full"
            v-model="parametros.semanaInicial"
          >
            <option
              v-for="(item, index) in arraySemanas"
              :key="index"
              :value="item"
            >
              {{ item }}
            </option>
          </select>
        </div>

        <!-- SEMANA FINAL -->
        <div class="w-1/2 md:w-1/12 p-2">
          <h1 class="font-bold uppercase w-full text-left">
            <span
              v-if="!obligatorios.semanaFinal"
              class="text-red-600 animated-pulse"
              >*
            </span>
            Semana final
          </h1>
          <select
            class="bg-white p-2 rounded shadow w-full"
            v-model="parametros.semanaFinal"
          >
            <option
              v-for="(item, index) in arraySemanas"
              :key="index"
              :value="item"
            >
              {{ item }}
            </option>
          </select>
        </div>

        <!-- VISTA -->
        <div class="w-1/2 md:w-1/12 p-2">
          <h1 class="font-bold uppercase w-full text-left">Vista</h1>
          <select
            class="bg-white p-2 rounded shadow w-full"
            v-model="parametros.vista"
          >
            <option value="0">VISUALIZAR TODOS</option>
            <option value="1">OCULTAR PLANES SIN REGISTROS</option>
          </select>
        </div>

        <!-- BOTÓN APLICAR -->
        <div class="flex justify-center items-center w-full md:w-1/12 p-2">
          <button
            v-if="!loading"
            class="
              flex
              bg-purple-200
              w-full
              p-2
              rounded
              text-purple-500
              font-bold
              text-center
              justify-center
              items-center
            "
            @click="gantt()"
          >
            Aplicar
          </button>

          <Loading
            v-if="loading"
            clases="w-full h-16 md:w-36 md:h-8 g-transparent md:bbg-purple-200 rounded"
          />
        </div>
      </div>
    </div>

    <div
      class="
        flex flex-col
        items-start
        justify-start
        w-full
        p-4
        overflow-x-auto
        text-xs
      "
    >
      <div
        class="
          flex flex-row
          items-center
          justify-start
          px-2
          w-auto
          fila
          bg-gray-800
          text-white
          sticky
          top-0
        "
      >
        <div class="w-56 flex-none md:w-80 font-bold text-center px-2">
          <h1>EQUIPOS</h1>
        </div>
        <div class="flex w-full flex-col items-start justify-center">
          <!-- fila de planes -->
          <div class="flex flex-row items-center justify-start">
            <div class="w-52">
              <h1 class="font-semibold uppercase">PLANES</h1>
            </div>
            <div class="flex flex-row items-center justify-center">
              <div
                v-for="(item, index) in rangoSemanas"
                :key="index"
                class="w-6 h-6 flex items-center justify-center relative"
              >
                {{ item }}
                <span
                  v-if="item === semanaActual"
                  class="w-3 h-3 rounded-full bg-red-600 absolute -bottom-1.5"
                ></span>
              </div>
            </div>
          </div>
          <!-- fila de planes -->
        </div>
      </div>

      <!-- SIN REGISTROS -->
      <div
        v-if="!array.equipos.length"
        class="w-full text-center uppercase font-extrabold text-lg py-5"
      >
        Sin Registros
      </div>

      <Filagantt
        v-for="(item, index) in array.equipos"
        :key="index"
        :array="item"
        :rangoSemanas="rangoSemanas"
      />
    </div>
  </div>
</template>

<script>
import Loading from "../components/Loading.vue";
import Filagantt from "../components/Filagantt.vue";

import { useStore } from "vuex";
import { onMounted, ref } from "@vue/runtime-core";

import getISOWeek from "date-fns/getISOWeek";

export default {
  components: { Filagantt, Loading },

  props: {},

  setup() {
    const store = useStore();

    const arrayDestinos = ref([]);
    const arraySecciones = ref([]);
    const arraySubsecciones = ref([]);
    const arrayTipos = ref([]);
    const arrayStatus = ref([]);
    const arrayFrecuencias = ref([]);
    const arrayAños = ref([]);
    const arraySemanas = ref([]);
    const array = ref({ equipos: [] });

    const obligatorios = ref({
      idDestino: true,
      idSeccion: true,
      semanaFinal: true,
      semanaInicial: true,
    });

    const loading = ref(false);
    const fechaHoy = new Date();
    const semanaActual = getISOWeek(
      new Date(fechaHoy.getFullYear(), fechaHoy.getMonth(), fechaHoy.getDate())
    );

    const rangoSemanas = ref([semanaActual]);

    const parametros = ref({
      idDestino: 0,
      idSeccion: 0,
      idSubseccion: 0,
      idTipo: 0,
      status: "TODOS",
      idFrecuencia: 0,
      año: fechaHoy.getFullYear(),
      semanaInicial: semanaActual,
      semanaFinal: semanaActual,
      vista: 1,
    });

    onMounted(() => {
      filtros();
    });

    const filtros = async () => {
      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "filtros",
      });

      if (response.response) {
        arrayDestinos.value = response.data.destinos;
        arraySecciones.value = response.data.secciones;
        arraySubsecciones.value = [];
        arrayTipos.value = response.data.tipos;
        arrayStatus.value = response.data.estados;
        arrayFrecuencias.value = response.data.frecuencias;
        arrayAños.value = response.data.años;
        arraySemanas.value = response.data.semanas;
      }
    };

    const subecciones = () => {
      arraySubsecciones.value = [];

      arraySecciones.value.forEach((element) => {
        if (element.idSeccion === parametros.value.idSeccion)
          arraySubsecciones.value = element.subsecciones;
      });
    };

    const gantt = async () => {
      // RANGO SEMANAS
      rangoSemanas.value = [];
      for (
        let index = parametros.value.semanaInicial;
        index <= parametros.value.semanaFinal;
        index++
      ) {
        rangoSemanas.value.push(index);
      }

      // CONDICIÓN DE DESTINO
      let idDestino = localStorage.getItem("idDestino");
      if (idDestino == 10) idDestino = parametros.value.idDestino;
      else parametros.value.idDestino = idDestino;

      // OBLIGATORIO DESTINO
      if (parametros.value.idDestino == 0) {
        obligatorios.value.idDestino = false;
        return;
      }

      // OBLIGATORIO SECCIÓN
      if (parametros.value.idSeccion == 0) {
        obligatorios.value.idSeccion = false;
        return;
      }

      // COMPROBACIÓN DE #SEMANAS
      if (
        parametros.value.semanaFinal < parametros.value.semanaInicial ||
        parametros.value.semanaInicial > parametros.value.semanaFinal
      ) {
        obligatorios.value.semanaInicial = false;
        obligatorios.value.semanaFinal = false;
        return;
      }

      obligatorios.value.semanaInicial = true;
      obligatorios.value.semanaFinal = true;

      array.value = { equipos: [] };
      loading.value = true;

      const data = {
        idDestino: idDestino,
        idUsuario: localStorage.getItem("usuario"),
        action: "gantt",
        semana: parametros.value.semanaInicial,
        idSeccion: parametros.value.idSeccion,
        idSubseccion: parametros.value.idSubseccion,
        idTipo: parametros.value.idTipo,
        status: parametros.value.status,
        idFrecuencia: parametros.value.idFrecuencia,
        año: parametros.value.año,
        semanaInicial: parametros.value.semanaInicial,
        semanaFinal: parametros.value.semanaFinal,
        vista: parametros.value.vista,
      };
      console.log(data);

      const response = await store.dispatch("accionesUsuario", data);

      loading.value = false;
      console.log(response);
      if (response.response) {
        array.value = response.data.data;
      }

      if (!response.response) {
        return;
      }
    };

    return {
      arrayDestinos,
      arraySecciones,
      arraySubsecciones,
      arrayTipos,
      arrayStatus,
      arrayFrecuencias,
      arrayAños,
      arraySemanas,
      array,
      parametros,
      loading,
      subecciones,
      gantt,
      rangoSemanas,
      semanaActual,
      obligatorios,
    };
  },
};
</script>